@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
:root {
  font-size: 14px;
  --bg-primary: 161616;
}

body {
  margin: 0 !important;
  padding: 0 !important;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
}

main {
  margin-left: 18.5rem;
  padding: 1.5rem 7.5rem 0 7.5rem;
  color: rgba(0, 0, 0, 1);
  background-color: #F6F7FB;
  height: 100%;
}

.primary-text {
  color: #007aff !important;
}

.mainContent {
  margin: 1.5rem 5rem 0 5rem;
}

.loginParentDiv {
  justify-content: center;
}

.loginChildDiv {
  background-color: white;
  border-radius: 12px;
  padding: 1rem 2rem 1rem 2rem;
  width: 70px !important;
}

.btn-outline-custom {
  color: #00A2E5;
  background-color: transparent;
  background-image: none;
  border-color: #00A2E5;
  width: 100% !important;
}

.btn-outline-custom:hover {
  transition: 0.5s;
  color: #fff;
  background-color: #00a5ee;
  border-color: #00a5ee;
}

button:disabled {
  cursor: not-allowed;
  pointer-events: all !important;
}

.svg-inline--fa:hover {
  cursor: pointer;
}

.flex-container {
  display: flex;
  justify-content: space-between;
}

.flex-item {
  padding: 0.5rem;
  border-radius: 5px;
  width: 49.5%;
  background-color: white;
  border: solid 1px rgba(0, 0, 0, 0.2);
}

.flex-item-cards {
  width: 24%;
  padding: 0.5rem;
  border-radius: 5px;
  background-color: white;
  border: solid 0.5px rgba(0, 0, 0, 0.2);
  transition: transform .3s;
}

.flex-item-cards-btn {
  width: 33%;
}

.flex-item-cards:hover {
  border: solid 0.5px;
  cursor: pointer;
  border-color: #00a5ee;
}

.flex-item-cards>p {
  text-align: center;
}

.boldText {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 1.2rem;
}

.custom-h6 {
  padding: 0 0 0.5rem 0;
  letter-spacing: 0.05rem;
  border-bottom: solid 1px rgba(0, 0, 0, 0.1);
  font-weight: 600;
}

.custom-titles {
  padding: 0 0 0.5rem 0;
  letter-spacing: 0.05rem;
  font-weight: 600;
}

#hoverUp {
  padding: 0.5rem;
  display: inline-block;
  background-color: 161616;
  background-color: var(--bg-primary);
  width: 2rem;
  height: 2rem;
  text-align: center;
  border-radius: 50px;
  position: fixed;
  bottom: 40px;
  right: 3rem;
  transition: background-color .3s, opacity .5s, visibility .5s;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  color: white;
}

#hoverUp:hover {
  cursor: pointer;
  background-color: #333;
}

#hoverUp.show {
  opacity: 1;
  visibility: visible;
}

.title-high {
  font-size: 2rem;
}

.hover-link {
  cursor: pointer;
}
.App-logo {
    width: 13%;
    height: auto;
}

#imgLogo {
    width: 75%;
    height: auto;
}

.profile-pic {
    height: 60px;
    min-width: 60px;
    margin: 0px 5px 0px 0px;
    border-radius: 50%;
    overflow: hidden;
    text-align: center;
    box-sizing: border-box;
    align-items: center;
}

.logo-pic {
    height: 5rem;
    width: 100%;
}

#imgLogo {
    -webkit-animation: fadeIn ease 3s;
            animation: fadeIn ease 3s;
}
.navbar {
  margin: 0;
  padding: .5rem 0 .5rem 0;
  width: 18.5rem;
  height: 100vh;
  position: fixed;
  background-color: #161616;
}

.navbarAnimation {
  -webkit-animation: slide ease 1.5s;
          animation: slide ease 1.5s;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
}

.nav-item {
  padding: 0 0 0 .51rem;
  width: 100%;
}

.nav-item:last-child {
  margin-top: auto;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 3.5rem;
  width: auto;
  color: white !important;
}

.nav-item:hover:not(:first-child):not(:last-child) {
  cursor: pointer;
  transition: 0.7s;
  box-shadow: -6px 0px #00a5ee inset;
  background-color: black;
}

.nav-item:first-child {
  padding-bottom: 1rem;
}

.section-items {
  font-size: 14px;
}

/* @Navbar horizontal */

.navbarHor {
  height: 4rem;
  margin-left: 18.5rem;
  background-color: white;
  border-bottom: solid 1px rgba(0, 0, 0, 0.1);
}

.fadeInAnimation {
  -webkit-animation: fadeIn ease-in-out 1.5s forwards;
          animation: fadeIn ease-in-out 1.5s forwards;
  -webkit-animation-delay: 1.3s;
          animation-delay: 1.3s;
}

.navbarHor-nav {
  list-style: none;
  padding: 0 7.5rem 0 7.5rem;
  margin: 0;
  display: flex;
  align-items: center;
  height: 100%;
}

.navHor-item {
  height: auto;
  width: 100%;
}

.horIcon:not(:last-child) {
  margin-right: 1rem;
  color: #1A2947;
}

.navHor-item:last-child {
  justify-content: flex-end;
  text-align: right;
}

.logo {
  margin-bottom: 1rem;
  width: 70%;
  height: 5rem;
  background-color: white;
  border-radius: 0.5rem;
}

.circleIconImg {
  width: 4%;
  height: 50%;
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes slide {
  from {
    left: -200px;
  }
  to {
    left: 0px;
  }
}

@keyframes slide {
  from {
    left: -200px;
  }
  to {
    left: 0px;
  }
}
